import React from 'react';
import AppCard from '@crema/core/AppCard';
import {Box, FormControl, Grid, MenuItem, Select} from '@mui/material';
import {Fonts} from 'shared/constants/AppEnums';
import {useIntl} from 'react-intl';
import AppAnimate from '@crema/core/AppAnimate';
import PropTypes from 'prop-types';
import {InputLabel} from '@mui/material';
import TextField from '@mui/material/TextField';

const AddOrderAddress = ({formData, setFormData}) => {
  const {messages} = useIntl();
  // const handleChangeState = (e) => {
  //   e.preventDefault();
  // };
  const handleChange = (e) => {
    e.preventDefault();
    setFormData({
      ...formData,

      [e.target.name]: e.target.value.trim(),
    });
  };

  return (
    <AppAnimate animation='transition.slideUpIn' delay={200}>
      <AppCard
        title={
          <Box fontSize={16} fontWeight={Fonts.BOLD}>
            {messages['sidebar.order.orderDetials']}
          </Box>
        }
      >
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mt: 2,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['Region']}: </Box>
          <Grid item textAlign={'start'} xs={6} sm={1.5}>
            <FormControl fullWidth>
              <InputLabel id='demo-simple-select-label'>
                {messages['Region']}
              </InputLabel>
              <Select
                labelId='demo-simple-select-label'
                id='address'
                defaultValue={''}
                name='address'
                label={['address']}
                onChange={(e) => handleChange(e)}
              >
                <MenuItem value={'westBank'}>الضفة الغربية</MenuItem>
                <MenuItem value={'jerusalem'}>القدس</MenuItem>
                <MenuItem value={'48Area'}>الداخل الفلسطبني</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['City']}: </Box>
          <Box>
            <TextField
              onChange={(e) => handleChange(e)}
              name='city'
              id='city'
              variant='standard'
            />
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>
            {messages['common.address']}:{' '}
          </Box>
          <Box>
            <TextField
              onChange={(e) => handleChange(e)}
              name='house'
              id='house'
              variant='standard'
            />
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['HouseType']}: </Box>
          <Box>
            <TextField
              onChange={(e) => handleChange(e)}
              name='houseType'
              id='houseType'
              variant='standard'
            />
          </Box>
        </Box>
      </AppCard>
    </AppAnimate>
  );
};

export default AddOrderAddress;

AddOrderAddress.propTypes = {
  formData: PropTypes.object.isRequired,
  setFormData: PropTypes.func.isRequired,
};
